.kik .top-bar {
    position: absolute;
    margin-left: auto;
    margin-right: auto;
    left: 0;
    right: 0;
}

.kik .filter {
    color: white;
    background-color: #f1bb3d;
    height: 50px;
    line-height: 50px;
    position: fixed;
    top: 60px;
    left: 0px;
    width: 100%;
    z-index: 9;
    font-size: 12px;
    border-top: 1px solid rgba(255, 255, 255, 0.1);
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    -webkit-box-shadow: 0px 3px 3px 0px rgba(0,0,0,0.15);
    -moz-box-shadow: 0px 3px 3px 0px rgba(0,0,0,0.15);
    box-shadow: 0px 3px 3px 0px rgba(0,0,0,0.15);

}

.kik .filter a.filter-close, .kik .filter a.filter-close:hover  {
    line-height: 19px;
    margin: 14px;
    position: absolute;
    right: 0;
    width: 18px;
    height: 18px;
    border-radius: 25px;
    background-color: rgba(0, 0, 0, 0.2);
    color: white;
    text-decoration: none;
}

.kik .data-box {
    background-color: white;
    border-radius: 3px;
    margin: 10px 10px 0;
    padding: 10px;
    -webkit-box-shadow: 0px 3px 3px 0px rgba(0,0,0,0.15);
    -moz-box-shadow: 0px 3px 3px 0px rgba(0,0,0,0.15);
    box-shadow: 0px 3px 3px 0px rgba(0,0,0,0.15);
}

.kik .job-box {
    background-color: white;
    border-radius: 3px;
    margin: 10px 10px 0;
    padding: 0 10px;
    height: 100px;
    overflow: hidden;
    position: relative;
    cursor: pointer;
    -webkit-box-shadow: 0px 3px 3px 0px rgba(0,0,0,0.15);
    -moz-box-shadow: 0px 3px 3px 0px rgba(0,0,0,0.15);
    box-shadow: 0px 3px 3px 0px rgba(0,0,0,0.15);
}

.kik .search {
    font-family: FontAwesome;
    width: 100%;
    text-align: center;
    line-height: 40px;
    color: #00b7f5;
    border-radius: 3px;
    border: 1px solid #e3e3e3;
}
.kik textarea, input[type="text"] {
    -webkit-appearance: none;
}
.kik .search::-webkit-input-placeholder { /* WebKit, Blink, Edge */
    color:    #00b7f5;
}
.kik .search:-moz-placeholder { /* Mozilla Firefox 4 to 18 */
   color:    #00b7f5;
   opacity:  1;
}
.kik .search::-moz-placeholder { /* Mozilla Firefox 19+ */
   color:    #00b7f5;
   opacity:  1;
}
.kik .search:-ms-input-placeholder { /* Internet Explorer 10-11 */
   color:    #00b7f5;
}

.kik .data-text {
    padding: 10px 10px 10px 0;
    width: 55%;
    display: inline-block;
}

.kik .data-img {
    position: absolute;
    top: 0;
    right: 0px;
    width: 45%;
    height: 100%;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}

.kik .title {
    color: #b3b3b3;
}

.kik .address {
    position: absolute;
    bottom: 10px;
    color: #d3d3d3;
    font-size: 12px;
}

.kik .address i {
    color: #b3b3b3;
}
.kik .job-indeed {
    position: absolute;
    right: 0;
    top: 10px;
    color: white;
    font-size: 9px;
    background: #2164f3;
    padding: 3px 9px;
}

.kik .job-monster {
    position: absolute;
    right: 0;
    top: 10px;
    color: white;
    font-size: 9px;
    background: #642896;
    padding: 3px 9px;
}

.kik .job-snagajob {
    position: absolute;
    right: 0;
    top: 10px;
    color: white;
    font-size: 9px;
    background: #f66f02;
    padding: 3px 9px;
}

.kik .scroll {
    margin-bottom: 10px;
}

.results-end {
    color: white;
    margin-top: 10px;
}

.no-loc-title {
    font-weight: bold;
    font-size: 11px;
}

.no-loc-text {
    color: #b3b3b3;
}

.kik .partner-job-header {
    position: fixed;
    height: 60px;
    width: 100%;
    line-height: 55px;
}

.kik iframe {
    margin-top: 60px;
    background-color: white;
    width: 100%;
    height: 100%;
    margin-top: 60px;
}

.kik .job-header {
    position: relative;
    color: white;
    height: 230px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}

.kik .job-header .back-icon {
    position: fixed;
    z-index: 9;
    width: 100%;
    background: -moz-linear-gradient(top, rgba(0,0,0,0.5) 0%, rgba(0,0,0,0) 100%); /* FF3.6-15 */
    background: -webkit-linear-gradient(top, rgba(0,0,0,0.5) 0%,rgba(0,0,0,0) 100%); /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to bottom, rgba(0,0,0,0.5) 0%,rgba(0,0,0,0) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#000000', endColorstr='#000000',GradientType=0 );
}

.kik .back-icon span {
    font-size: 15px;
    color: white;
}

.kik .back-icon i {
    font-size: 15px;
    font-weight: 700;
    margin: 15px 10px;
    color: white;
}

.kik .job-header-text {
    width: 100%;
    position: absolute;
    font-size: 12px;
    color: white;
    bottom: 0;
    background: -moz-linear-gradient(top, rgba(0,0,0,0) 0%, rgba(0,0,0,0.6) 100%); /* FF3.6-15 */
    background: -webkit-linear-gradient(top, rgba(0,0,0,0) 0%,rgba(0,0,0,0.6) 100%); /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to bottom, rgba(0,0,0,0) 0%,rgba(0,0,0,0.6) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00000000', endColorstr='#000000',GradientType=0 );
    //background-color: rgba(0, 0, 0, 0.3);
    padding-bottom: 5px;
    padding-top: 15px;
}

.kik .job-header-title {
    font-size: 14px;
    font-weight: bold;
}

.kik .job-body {
    background-color: #f7f7f7;
    padding-bottom: 60px;
}

.kik .job-body .data-box {
    border: 1px solid rgba(0, 0, 0, 0.1);
    color: #b3b3b3;
}

.kik .job-body-heading {
    color: black;
    font-size: 10px;
    font-weight: bold;
}

.kik .job-body-details {
    line-height: 20px;
    //margin-bottom: 20px;
}

.kik .quick-apply {
    position: fixed;
    bottom: 0;
    height: 60px;
    background-color: rgba(0, 169, 224, 0.9);
    //background-color: #02a8e0;
    width: 100%;
    color: white;
    font-size: 11px;
    line-height: 15px;
}

.kik .quick-apply-btn {
    position: absolute;
    right: 10px;
    height: 30px;
    top: 15px;
    width: 90px;
    color: white;
    background-color: transparent;
    border: 2px solid rgba(0, 0, 0, 0.1);
    border-radius: 5px;
}
.kik .quick-apply-text {
    padding-top: 15px;
    padding-left: 44px;
    display: inline-block;
}

.lightweight {
    font-weight: 300;
}
.underline, .underline:hover {
    text-decoration: underline;
}

.kik .details-data-box{
    margin: 0px 0px 0;
}
